#basket_app {
	display: inline-block;
}

#cart_check_all {
	z-index: 515;
	text-align: left;
	height: 350px;
	margin-right: 30px;
}

#cart_check_all .b-button__close-popup {
	right: -30px;
}

.basket_items_wrap {
	background: #fff;
	padding: 0;
	-webkit-box-shadow: 0 0 62px rgba(0, 0, 0, 0.2);
	box-shadow: 0 0 62px rgba(0, 0, 0, 0.2);
	margin-bottom: 20px;
	width: 430px;
	height: 90vh;
	z-index: 510;
	text-align: left;
	margin-right: 10px;
	overflow: hidden;
	position: relative;
}

.basket_items_wrap .head_bottom_line {
	width: 100%;
	height: 5px;
	background: url(/local/dist/images/products-top_bottom_reverce.png) 50% 50% repeat-x;
	display: block;
	position: relative;
	z-index: 100;
	top: -5px;
	margin-bottom: -5px;
}

.basket_items_wrap .bs_item_messages {
	position: relative;
}

.basket_items_wrap .bs_item_messages .bs_item_message.danger {
	background: red;
	color: #fff;
	padding: 5px 10px;
}

.basket_items_wrap .bs_shop_basket_items {
	width: 100%;
	border-bottom: 1px solid #e6e6e6;
}

.basket_items_wrap .bs_shop_basket_items .bs_basket_item {
	padding: 15px 10px;
}

.basket_items_wrap .bs_shop_basket_items .bs_basket_item:hover .b-product__count {
	display: block;
	top: auto;
	margin-top: -33px;
}

.basket_items_wrap .bs_shop_basket_items .bs_basket_item .bs_wrap_item {
	padding: 25px 25px 25px 15px;
	border-radius: 5px;
}

.basket_items_wrap .bs_shop_basket_items .bs_basket_item:hover .bs_wrap_item {
	box-shadow: inset 0 0 0 3px #fdd000;
	transition: 0.3s;

}

.basket_items_wrap .bs_shop_basket_items .bs_basket_item:hover .b-product__count .b-count__in {
	opacity: 1;
	transition: 0.3s;
}

.basket_items_wrap .bs_shop_basket_items .bs_basket_item .bs_item_info {
	display: flex;
}

.basket_items_wrap .bs_shop_basket_items .bs_basket_item .bs_item_info .bs_item_img {
	width: 130px;
	text-align: center;
}

.basket_items_wrap .bs_shop_basket_items .bs_basket_item .bs_item_info .bs_item_img img {
	max-width: 70px;
	height: auto;
}

.basket_items_wrap .bs_shop_basket_items .bs_basket_item .bs_product_item {
	display: flex;
	flex-direction: column;
}

.basket_items_wrap .bs_shop_basket_items .bs_basket_item .bs_product_item .bs_product_actions {
	display: flex;
	align-items: center;
}

.bs_basket_item .bs_product_name {
	margin-bottom: 20px;
}

.bs_basket_item .bs_product_actions .bs_product_price {
	font-size: 1.5em;
	flex: 4;
	text-align: center;
}

.bs_basket_item .bs_product_actions .bs_counter {
	flex: 4;
}

.bs_basket_item .bs_product_actions .bs_counter .b-product-preview__count_cart {
	width: 96%;
	margin-left: 0;
}

.bs_basket_item .bs_product_actions .bs_product_delete {
	flex: 3;
}

.bs_basket_item .b-product-textarea, .bs_basket_item .cart-com-buttons {
	display: block;
}

.basket_items_wrap .bs_shop_header {
	display: flex;
	padding: 10px 10px 15px 10px;
	position: relative;
	z-index: 100;
}

.basket_items_wrap .bs_shop_header .bs_shop_img {
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 85px;
	align-items: center;
}

.basket_items_wrap .bs_shop_header .bs_shop_img img {
	width: 60px;
}

.basket_items_wrap .bs_shop_header .bs_shop_info {
	display: flex;
	flex-direction: column;
	width: 100%;
}

.basket_items_wrap .bs_shop_header .bs_shop_info .cart__cl-50 {
	width: 52%;
}

.basket_items_wrap .bs_shop_header .bs_shop_info .cart__cl-50 a {
	text-decoration: none !important;
}

.basket_items_wrap .bs_shop_header .cart__deliv {
	display: flex;
}

.basket_items_wrap .bs_shop_header .search__title {
	font-size: 20px;
}

.basket_items_wrap .bs_shop_header .search__title .cart__title-item {
	font-size: 13px;
}

.modal_content.wrap_basket_modal {
	justify-content: flex-end;
	top: 2%;
}

.bs_shop_basket_items .el-loading-mask {
	z-index: 600;
	opacity: 0.9;
}

.bs_shop_basket_items .bs_product_delete .b-button__del {
	position: relative;
	z-index: 610;
}

.b-popup-check .b-button_green {
	padding: 5px 20px;
	height: auto;
	margin-top: 15px;
	text-decoration: none;
}

.b-popup-check .b-button_green:hover {
	background: linear-gradient(to bottom, #32b95c 0%, #66b83f 100%);
	color: #fff;
}

.b-popup-check .b-button_green.disabled {
	background: rgba(50, 185, 92, 0.51);
	cursor: default;
}

.b-header-main.b-ib-wrapper {
	display: flex;
}

.b-header-main__right {
	width: 100%;
}

.b-footer-bottom {
	display: flex;
	justify-content: space-around;
}

.b-footer-bottom .bottom_item {
	width: 25%;
}

.bottom_item .b-footer-bottom__info {
	width: 100%;
	padding-left: 0;
	padding-top: 15px;
	font-size: 14px;
}

.popup_replace_2 {
	width: 430px;
	background: #fff;
	position: relative;
	margin: 3% auto 0;
	z-index: 710;
	min-height: 570px;
}

.replace_2_header {
	width: 100%;
	height: 250px;
	background: url(/local/dist/images/r_bottom_border_w.png) left bottom #ffda00 repeat-x;
}

.popup_replace_2 .container {
	width: 90%;
	margin: 0 5%;
	overflow: hidden;
}

.replace_2_body h2 {
	font-size: 30px;
	font-family: 'HelveticaNeueCyr-Thin', "Open Sans", Arial, "Helvetica Neue", Helvetica, sans-serif;
	margin: 10px 0 20px 0;
	padding: 10px 0 17px 0;
	border-bottom: 1px solid #e5e5e5;
	text-align: left;
}

.popup_replace_2 .container p {
	margin: 10px 0;
	line-height: 22px;
	color: #373737;
	font-size: 14px;
	text-align: left;
}

.popup_replace_2 .btn_group {
	text-align: center;
	margin-top: 25px;
}

#free_delivery .popup_replace_2 .btn_group {
	text-align: left;
}

.replace_btn_.confirm_orders {
	background: #34c263;
	color: #fff;
	padding: 0 14px;
}

.replace_btn_ {
	height: 33px;
	font-size: 14px;
	line-height: 33px;
	border-radius: 30px;
	background: #e5e5e5;
	padding: 0 14px;
	color: #373737;
	text-decoration: none;
	margin: 0 7px;
}

.replace_btn_.confirm_orders:before {
	content: '';
	background-image: url(/local/dist/images/sprite2.png);
	background-position: -736px -519px;
	width: 20px;
	height: 20px;
	display: inline-block;
	vertical-align: middle;
	margin-right: 6px;
	position: relative;
	top: -2px;
}

.b-product-preview__price {
	display: block;
}

.b-button.b-button_green.add_basket_btn {
	width: 180px;
}

.product_price_wrap {
	display: flex;
	justify-content: space-around;
}
.b-product-preview__price {
	//font-size: 18px;
}
.b-product-preview-quantity {
	display: flex;
	font-size: 13px;
	align-items: center;

	.quantity_input {
		//height: 30px;
	}

	.q-minus, .q-plus {
		width: 60px;
		height: 30px;
		background: #f3f2f0;
		border-radius: 50%;
		display: flex;
		align-items: center;
		text-decoration: none;
		justify-content: center;

		&:hover {
			opacity: 0.7;
		}
	}

	.q-minus {
		margin-right: 10px;
	}
	.q-plus {
		margin-left: 10px;
		background: #34c263;
		color: #fff;
	}

}



.b-product-preview-quantity {
	display: flex;
	font-size: 13px;
	align-items: center;
	position:relative;
}

.b-product-preview-quantity .input-measure {
	position: absolute;
	right: 52px;
	bottom: 3px;
	font-size: 12px;
}


